import React, {useState, useEffect} from 'react'
import './comp.css'
import axios from 'axios'

import qs from 'qs'

export default function LoginBox(props){




    const [user_name, setUser] = useState()
    const [password , setPassword] = useState()
    const [flag , setFlag] = useState(1)
    const authOptions = {
     method: 'post',
     url: 'http://127.0.0.1:8000/api/autorization/',
     data: [{ "login" : user_name , "password" : password }],
     headers: {
       'Content-Type': 'application/json'
      },
     json: true
    };
    const mouseClickHandler = () => {
    axios(authOptions).then((response) => {
        setFlag(response)
        })
    }


    return(
    <div>
        <div>
            <h1 className = 'words'> LOGIN </h1>
        </div>
        <div className = 'elem'>
            <label className="words">Username</label>
            <input
              type="text"
              name="username"
              className = 'text_box'
              placeholder="Username"
              onChange = {(e) => setUser(e.target.value)}
             />
          </div>
          <div className = 'elem'>
            <label className="words">Password</label>
            <input
              type="text"
              name="password"
              className = 'text_box , passwd'
              placeholder="Password"
              onChange = {(e) => setPassword(e.target.value)}
             />
          </div>
          <button
            type="button"
            className="login-btn"
            onClick = {mouseClickHandler}>Login
           </button>
           <label  >       {flag}</label>


    </div>
    )

}


